import { useState, useEffect, lazy } from 'react';
import PropTypes from 'prop-types';
import Loadable from 'ui-component/Loadable';
const Photo = Loadable(lazy(() => import('views/photo')));

// material-ui
import {
    Button,
    Dialog,
    DialogTitle,
    FormControlLabel,
    ImageList,
    ImageListItem,
    ImageListItemBar,
    Input,
    IconButton,
    Tooltip
} from '@mui/material';
import { StarBorder as StarBorderIcon, SwitchLeft as SwitchLeftIcon, CloudQueue as CloudQueueIcon } from '@mui/icons-material';

function PhotoGallery({ xmlSchema, onModified, productModified, photoCols, rowHeight, xmlGetField, langPack }) {
    let [imageObj, setImageObj] = useState({}); //
    let [cloudImageArr, setCloudImageArr] = useState([]); //
    let [dialogOpen, setDialogOpen] = useState(false); //

    useEffect(() => {
        if (!xmlSchema) return;
        let { scImages } = productModified;
        scImages ? setImageObj(scImages) : getImageObj();
        return;
    }, [xmlSchema]);

    function getImageObj() {
        let fieldObj = xmlGetField('scImages');
        //console.log(fieldObj);
        if (!fieldObj) return;
        if (fieldObj.values) {
            fieldObj._values = fieldObj.values
                .map((obj) => {
                    let key = obj.id;
                    let value = obj.values[0].value.replace(/_.*/, ''); //
                    return { key, value };
                })
                .sort((a, b) => Number(/\d/.exec(a.key)[0]) - Number(/\d/.exec(b.key)[0])); //
        }
        return setImageObj(fieldObj);
    } //

    function onImageChange(method, valueArr) {
        if (!valueArr.length) return; //
        let newObj = { ...imageObj };
        let imageArr = newObj._values || [];
        let newArr = [];
        if (method === 'add') {
            newArr = [...imageArr, ...valueArr];
            if (newArr.length > 6) newArr.length = 6;
        } else if (method === 'remove') {
            if (imageArr.length <= 1) return queryResult(false, langPack.steps.mainPhotoRequired);
            valueArr.forEach((valueObj) => {
                newArr = imageArr.filter((obj) => obj.value !== valueObj.value);
            });
        } else if (method === 'reorder') {
            newArr = valueArr;
        }
        newArr = newArr.map((obj, index) => {
            return {
                ...obj,
                key: `scImages_${index}`,
                value: obj.value.replace('https:', '')
            };
        }); //
        newArr = newArr.filter((obj) => obj.id); // "fileId;" 
        if (!newArr.length) setDialogOpen(true);
        newObj._values = newArr;
        let valueTagName = `complex-value`;
        let setter = setImageObj;
        let newField = new DOMParser().parseFromString(newObj.xml.outerHTML, 'text/xml').firstChild; //
        let valueTemplates = newField.querySelectorAll('fields > field'); //
        let valueSaver = `<${valueTagName}>`; //() //type: input
        newArr.forEach((obj, index) => {
            valueTemplates[index].innerHTML = `<value fileId="${obj.id}">${obj.value}</value>`;
            valueSaver += valueTemplates[index].outerHTML;
        });
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('scImages', newObj); //
    } //

    //Cloud-Photo 
    let onDialogClose = (submit) => {
        setDialogOpen(false);
        if (submit) {
            let valueArr = cloudImageArr.map((obj) => {
                return { ...obj, key: obj.id, value: obj.url };
            });
            return onImageChange('add', valueArr);
        }
        return setCloudImageArr([]); //
    }; //
    let onPhotoSelected = (value) => {
        if (!value) return;
        setCloudImageArr(value);
    }; //component

    let onPhotoReorder = (index) => {
        let newObj = { ...imageObj };
        let newArr = newObj._values;
        let deletedImage = newArr.splice(index, 1);
        newArr.splice(index - 1, 0, deletedImage[0]);
        return onImageChange('reorder', newArr);
    };

    return (
        <ImageList cols={photoCols} rowHeight={rowHeight} sx={{ marginTop: '10px', marginBottom: '10px' }} variant="quilted" gap={2}>
            {imageObj._values && imageObj._values.length ? (
                imageObj._values.map((obj, index) => {
                    return (
                        <ImageListItem key={index} cols={1} rows={1}>
                            <img src={`https:` + obj.value} alt={index + 1} loading="lazy" />
                            <ImageListItemBar
                                sx={{
                                    background:
                                        'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' + 'rgba(0,0,0,0.3) 50%, rgba(0,0,0,0) 100%)'
                                }}
                                title={index + 1}
                                position="top"
                                actionIcon={
                                    <Tooltip title={langPack.steps.removePhotos}>
                                        <IconButton
                                            sx={{ color: index ? 'yellow' : 'white' }}
                                            aria-label={`star ${index + 1}`}
                                            onDoubleClick={() => onImageChange('remove', [obj])}
                                        >
                                            <StarBorderIcon />
                                        </IconButton>
                                    </Tooltip>
                                }
                                actionPosition="left"
                            />
                            {index && obj.id ? (
                                <Button
                                    sx={{ position: 'absolute', right: '0px', top: '5px' }}
                                    size="large"
                                    color="warning"
                                    onClick={() => onPhotoReorder(index)}
                                    startIcon={<SwitchLeftIcon />}
                                />
                            ) : null}
                        </ImageListItem>
                    );
                })
            ) : (
                <></>
            )}
            {!imageObj._values || imageObj._values.length < 6 ? (
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                    <Tooltip title={langPack.steps.selectFromCloud}>
                        <Button sx={{ height: '100%' }} startIcon={<CloudQueueIcon />} onClick={() => setDialogOpen(true)}>
                            {langPack.steps.cloud}
                        </Button>
                    </Tooltip>
                    <Dialog onClose={() => onDialogClose(true)} open={dialogOpen}>
                        <DialogTitle>{langPack.steps.selectFromCloud}</DialogTitle>
                        <Photo onPhotoSelected={onPhotoSelected} />
                        <Button variant="contained" color="primary" onClick={() => onDialogClose(true)}>
                            {langPack.common.yes}
                        </Button>
                        <Button variant="contained" color="error" onClick={() => onDialogClose()}>
                            {langPack.common.no}
                        </Button>
                    </Dialog>
                </div>
            ) : null}
        </ImageList>
    );
}

PhotoGallery.propTypes = {
    xmlSchema: PropTypes.object,
    productModified: PropTypes.object,
    photoCols: PropTypes.number,
    rowHeight: PropTypes.number,
    onModified: PropTypes.func,
    xmlGetField: PropTypes.func,
    langPack: PropTypes.object
};

export default PhotoGallery;
