import { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Typography, Autocomplete, Button, Checkbox, InputAdornment, MenuItem, TextField, Divider, Tooltip, Chip } from '@mui/material';
import { Add as AddIcon, Check as CheckIcon, Cancel as CancelIcon, DeleteOutline as DeleteIcon } from '@mui/icons-material';

function App({ langPack, onModified, productModified, xmlSchema, xmlGetField }) {
    console.log('service running');
    let customSaver = '';
    let [customObj, setCustomObj] = useState({}); //
    let [addCustom, setAddCustom] = useState(false); //
    let [customSelected, setCustomSelected] = useState(''); //
    let [customStatusObj, setCustomStatusObj] = useState({}); //

    useEffect(() => {
        if (!xmlSchema) return;
        let { customizedServices, productLightCustom } = productModified;
        customizedServices ? setCustomObj(customizedServices) : getCustomObj();
        productLightCustom ? setCustomStatusObj(productLightCustom) : getCustomStatusObj();
        return;
    }, [xmlSchema]);

    function getCustomStatusObj() {
        let fieldObj = xmlGetField('productLightCustom');
        //console.log(fieldObj);
        if (!fieldObj) return;
        fieldObj._options = fieldObj.options
            ? fieldObj.options.map((obj) => {
                  return { key: obj.displayName, value: obj.value };
              })
            : []; //
        if (fieldObj.values) {
            fieldObj._values = { value: fieldObj.values[0].value }; //
            fieldObj._values.key = fieldObj._options.find((obj) => obj.value === fieldObj._values.value).key;
        } else {
            fieldObj._values = {};
        }
        return setCustomStatusObj(fieldObj);
    } //
    function onCustomStatusChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productCustomizationNeeded); //
        if (!value || !value.trim()) return queryResult(false, ''); //
        let newObj = { ...customStatusObj };
        let setter = setCustomStatusObj;
        let valueTagName = 'value';
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += value;
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('productLightCustom', newObj); //
    } //

    function getCustomObj() {
        let fieldObj = xmlGetField('customizedServices');
        if (!fieldObj) return;
        if (fieldObj.values) {
            fieldObj._values = fieldObj.values
                .filter((obj) => obj.type === 'complex')
                .map((obj) => {
                    return { key: obj.values[1].values[0].value, value: Number(obj.values[0].values[0].value) };
                });
        }
        let optionArr = fieldObj.options.filter((obj) => obj.type === 'complex');
        if (optionArr.length) {
            fieldObj._options = optionArr[0].options[1].options
                .filter((obj) => obj.value !== '-1') // displayName: "other" ;
                .map((obj) => {
                    return { ...obj, key: obj.displayName, value: obj.value };
                });
        } //
        //console.log(fieldObj);
        return setCustomObj(fieldObj);
    } //

    const onCustomOptionChange = (value) => {
        setCustomSelected(value);
        return;
    }; //

    const onCustomOptionConfirm = () => {
        if (!customSelected || !customSelected.trim()) return;
        let newObj = { ...customObj };
        let optionFound = newObj._options.find((obj) => obj.key === customSelected);
        let key = optionFound ? optionFound.value : customSelected;
        if (!newObj._values) newObj._values = []; //
        let valueArr = newObj._values.filter((obj) => obj.key !== key);
        if (valueArr.length >= 5) return;
        valueArr.push({ key, value: 1 });
        newObj._values = valueArr;
        setCustomObj(newObj);
        console.log(newObj);
        setCustomSelected('');
        setAddCustom((prev) => !prev);
        return;
    }; //

    const onCustomChange = (obj) => {
        if (!customSaver) customSaver = {};
        let finalValue = Number(obj.value);
        if (isNaN(finalValue)) return queryResult(false, langPack.steps.numberOnly);
        customSaver = { key: obj.key, value: finalValue };
        return;
    }; //

    const onCustomConfirm = () => {
        if (!customSaver) return;
        let newObj = { ...customObj };
        let valueArr = newObj._values.filter((obj) => obj.key !== customSaver.key);
        valueArr = valueArr.filter((obj) => obj.value);
        valueArr = valueArr.filter((obj) => !isNaN(obj.value));
        valueArr.push(customSaver);
        customSaver = '';
        newObj._values = valueArr;
        let setter = setCustomObj;
        let valueTagName = `complex-value`;
        let newField = new DOMParser().parseFromString(newObj.xml.outerHTML, 'text/xml').firstChild; //
        let valueTemplates = newField.querySelectorAll(`fields > field[type=complex]`); //
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueArr.forEach((obj, index) => {
            valueTemplates[index].querySelector('fields > field#custom_type').innerHTML = `<value>${obj.key}</value>`;
            valueTemplates[index].querySelector('fields > field#quantity').innerHTML = `<value>${obj.value}</value>`;
            valueSaver += valueTemplates[index].outerHTML;
        });
        valueSaver += `</${valueTagName}>`;
        valueSaver = valueSaver.replace(/fields/g, valueTagName); //
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        onModified('customizedServices', newObj);
        onCustomStatusChange('1'); //
        return;
    }; //

    const onCustomClear = () => {
        let newObj = { ...customObj };
        newObj._values = newObj._values.filter((obj) => obj.value);
        setCustomObj(newObj);
        return;
    }; //

    return (
        <>
            <div style={{ paddingTop: '10px', paddingBottom: '20px' }}>
                {!addCustom ? (
                    <>
                        <Tooltip title={langPack.steps.addCustomInfo}>
                            <Button startIcon={<AddIcon />} onClick={() => setAddCustom((prev) => !prev)}>
                                {langPack.steps.addCustomInfo}
                            </Button>
                        </Tooltip>
                        <Tooltip title={langPack.steps.clearCustomInfo}>
                            <Button startIcon={<DeleteIcon />} onClick={onCustomClear}>
                                {langPack.steps.clearCustomInfo}
                            </Button>
                        </Tooltip>
                    </>
                ) : (
                    <span style={{ display: 'flex', flexDirection: 'row', alignItems: 'stretch' }}>
                        {customObj._options && customObj._options.length ? (
                            <Autocomplete
                                id="add-custom"
                                freeSolo
                                key={customObj._options || 'options'}
                                sx={{ display: 'inline' }}
                                onChange={(e, value) => onCustomOptionChange(value)}
                                onInputChange={(e, value) => onCustomOptionChange(value)}
                                options={customObj._options.map((obj) => obj.key)}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        fullWidth={false}
                                        sx={{ marginTop: '13px', minWidth: '220px' }}
                                        label={<Typography variant="h3">{customObj.name}</Typography>}
                                        placeholder={langPack.form.selectOrInput}
                                    />
                                )}
                            />
                        ) : (
                            <></>
                        )}
                        <Button startIcon={<CheckIcon />} onClick={() => onCustomOptionConfirm()}>
                            {langPack.common.yes}
                        </Button>
                        <Button startIcon={<CancelIcon />} onClick={() => setAddCustom((prev) => !prev)}>
                            {langPack.common.no}
                        </Button>
                    </span>
                )}
            </div>
            <Divider />
            <br />
            {customObj._values && customObj._values.length ? (
                customObj._values.map((obj) => (
                    <Tooltip title={langPack.table.min_order_quantity}>
                        <TextField
                            key={obj.key}
                            type="number"
                            fullWidth={false}
                            defaultValue={obj.value}
                            onChange={(e) => {
                                onCustomChange({ key: obj.key, value: e.target.value });
                            }}
                            onBlur={onCustomConfirm}
                            label={<Typography variant="h3">{obj.key}</Typography>}
                            placeholder={langPack.form.selectOrInput}
                        />
                    </Tooltip>
                ))
            ) : (
                <></>
            )}
        </>
    );
}

App.propTypes = {
    langPack: PropTypes.object,
    xmlSchema: PropTypes.object,
    productModified: PropTypes.object,
    xmlGetField: PropTypes.func,
    onModified: PropTypes.func
};

export default App;
