import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

// material-ui
import {
    Typography,
    Autocomplete,
    Button,
    Checkbox,
    InputAdornment,
    MenuItem,
    TextField,
    Divider,
    Tooltip,
    Chip,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper
} from '@mui/material';
import {
    Add as AddIcon,
    Remove as RemoveIcon,
    Check as CheckIcon,
    Cancel as CancelIcon,
    ModeEdit as EditIcon,
    Clear as ClearIcon,
    DeleteOutline as DeleteIcon
} from '@mui/icons-material';

function App({ xmlSchema, langPack, productModified, xmlGetField, onModified }) {
    console.log('RTS running');
    //
    let [shippingTemplateObj, setShippingTemplateObj] = useState({});
    //
    let [weightObj, setWeightObj] = useState({});
    //
    let [measureObj, setMeasureObj] = useState({});
    //
    let [saleTypeObj, setSaleTypeObj] = useState({});
    //
    let [batchObj, setBatchObj] = useState({});

    useEffect(() => {
        if (!xmlSchema) return;
        let { shippingTemplateId, pkgWeight, pkgMeasure, saleType, batchNum } = productModified;
        shippingTemplateId ? setShippingTemplateObj(shippingTemplateId) : getShippingTemplateObj();
        pkgWeight ? setWeightObj(pkgWeight) : getWeightObj();
        pkgMeasure ? setMeasureObj(pkgMeasure) : getMeasureObj();
        saleType ? setSaleTypeObj(saleType) : getSaleTypeObj();
        batchNum ? setBatchObj(batchNum) : getBatchObj();
        return;
    }, [xmlSchema]);

    function getShippingTemplateObj() {
        let fieldObj = xmlGetField('shippingTemplateId');
        //console.log(fieldObj);
        if (!fieldObj) return;
        fieldObj._options = fieldObj.options
            ? fieldObj.options.map((obj) => {
                  return { key: obj.displayName, value: obj.value };
              })
            : []; //
        if (fieldObj.values) {
            fieldObj._values = { value: fieldObj.values[0].value }; //
            fieldObj._values.key = fieldObj._options.find((obj) => obj.value === fieldObj._values.value).key;
        } else {
            //productTypeSaver = fieldObj;
        }
        return setShippingTemplateObj(fieldObj);
    } //

    const onShippingTemplateChange = (valueObj) => {
        if (!valueObj) return queryResult(false, langPack.steps.productSubjectNeeded); //
        let newObj = { ...shippingTemplateObj };
        newObj._values = valueObj;
        let valueTagName = `value`;
        let setter = setShippingTemplateObj;
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += valueObj.value;
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('shippingTemplateId', newObj); //
    }; //

    function getWeightObj() {
        let fieldObj = xmlGetField('pkgWeight');
        //console.log(fieldObj);
        if (!fieldObj) return;
        if (fieldObj.values) {
            fieldObj._values = { value: fieldObj.values[0].value };
        }
        return setWeightObj(fieldObj);
    } //

    const onWeightChange = (value) => {
        if (!value) return queryResult(false, langPack.steps.productWeightNeeded); //
        let newObj = { ...weightObj };
        newObj._values = { value };
        let valueTagName = `value`;
        let setter = setWeightObj;
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += value;
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('pkgWeight', newObj); //
    };

    function getMeasureObj() {
        let fieldObj = xmlGetField('pkgMeasure');
        //console.log(fieldObj);
        if (!fieldObj) return;
        if (fieldObj.values) {
            fieldObj._values = fieldObj.values.map((obj) => {
                return { key: obj.id, value: obj.values[0].value };
            });
            let ranking = ['length', 'width', 'height'];
            fieldObj._values.sort((a, b) => {
                return ranking.indexOf(a.key) - ranking.indexOf(b.key);
            });
        }
        return setMeasureObj(fieldObj);
    } //

    const onMeasureChange = (value) => {
        if (!value) return queryResult(false, langPack.steps.productMeasureNeeded); //
        let arr = value.split('*');
        if (arr.length < 3) return queryResult(false, langPack.steps.productMeasureNeeded); //
        let hasError = false;
        arr.forEach((num) => {
            if (!num || isNaN(num)) hasError = true;
        });
        if (hasError) return queryResult(false, langPack.steps.productMeasureNeeded); //
        let newObj = { ...measureObj };
        let keyArr = ['length', 'width', 'height'];
        newObj._values = arr.map((obj, index) => {
            return { key: keyArr[index], value: Number(obj) };
        });
        let valueTagName = `complex-value`;
        let setter = setMeasureObj;
        let newField = new DOMParser().parseFromString(newObj.xml.outerHTML, 'text/xml').firstChild; //
        let valueTemplates = newField.querySelectorAll('fields > field'); //
        let valueSaver = `<${valueTagName}>`; //() //type: input
        let allValueTemplates = Array.from(valueTemplates);
        allValueTemplates.find((node) => node.id === 'length').innerHTML = `<value>${newObj._values[0].value}</value>`;
        allValueTemplates.find((node) => node.id === 'width').innerHTML = `<value>${newObj._values[1].value}</value>`;
        allValueTemplates.find((node) => node.id === 'height').innerHTML = `<value>${newObj._values[2].value}</value>`;
        allValueTemplates.forEach((node) => {
            valueSaver += node.outerHTML;
        });
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('pkgMeasure', newObj);
    }; //

    function getSaleTypeObj() {
        let fieldObj = xmlGetField('saleType');
        //console.log(fieldObj);
        if (!fieldObj) return;
        fieldObj._options = fieldObj.options
            ? fieldObj.options.map((obj) => {
                  return { key: obj.displayName, value: obj.value };
              })
            : []; //
        if (fieldObj.values) {
            fieldObj._values = { key: '', value: fieldObj.values[0].value };
            fieldObj._values.key = fieldObj._options.find((obj) => obj.value === fieldObj._values.value).key;
        }
        return setSaleTypeObj(fieldObj);
    } //

    const onSaleTypeChange = (valueObj) => {
        if (!valueObj) return queryResult(false, langPack.steps.productTypeNeeded); //
        let newObj = { ...saleTypeObj };
        newObj._values = valueObj;
        let valueTagName = `value`;
        let setter = setSaleTypeObj;
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += valueObj.value;
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('saleType', newObj); //
    };

    function getBatchObj() {
        let fieldObj = xmlGetField('batchNum');
        //console.log(fieldObj);
        if (!fieldObj) return;
        if (fieldObj.values) {
            fieldObj._values = { value: fieldObj.values[0].value };
        }
        return setBatchObj(fieldObj);
    } //

    const onBatchChange = (value) => {
        if (!value) return queryResult(false, langPack.steps.productWeightNeeded); //
        let newObj = { ...batchObj };
        newObj._values = { value };
        let valueTagName = `value`;
        let setter = setBatchObj;
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += value;
        valueSaver += `</${valueTagName}>`;
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('batchNum', newObj); //
    };

    return (
        <>
            <Autocomplete
                id="select-shipping-template"
                key={JSON.stringify(shippingTemplateObj._values)}
                sx={{ display: 'inline' }}
                onChange={(e, value) => onShippingTemplateChange(value)}
                defaultValue={shippingTemplateObj._values}
                options={shippingTemplateObj._options || []}
                getOptionLabel={(option) => option.key}
                isOptionEqualToValue={(option, value) => option.value === value.value}
                renderOption={(props, option, { selected }) => (
                    <li {...props}>
                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                        {option.key}
                    </li>
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        fullWidth={false}
                        sx={{ marginTop: '13px', minWidth: '220px' }}
                        label={<Typography variant="h3">{shippingTemplateObj.name}</Typography>}
                        placeholder={langPack.form.selectOrInput}
                    />
                )}
            />
            <Tooltip title={langPack.steps.numberOnly}>
                <TextField
                    margin="normal"
                    type="number"
                    key={weightObj._values}
                    id="port"
                    variant="outlined"
                    label={<Typography variant="h3">{weightObj.name}</Typography>}
                    defaultValue={weightObj._values ? weightObj._values.value : ''}
                    onBlur={(e) => onWeightChange(e.target.value)}
                />
            </Tooltip>
            <Tooltip title={langPack.form.measure}>
                <TextField
                    margin="normal"
                    key={JSON.stringify(measureObj._values)}
                    id="measure"
                    variant="outlined"
                    label={<Typography variant="h3">{measureObj.name}</Typography>}
                    defaultValue={
                        measureObj._values
                            ? `${measureObj._values[0].value} * ${measureObj._values[1].value} * ${measureObj._values[2].value}`
                            : ''
                    }
                    onBlur={(e) => onMeasureChange(e.target.value)}
                />
            </Tooltip>
            <Autocomplete
                id="add-sale-type"
                key={JSON.stringify(saleTypeObj._values)}
                sx={{ display: 'inline' }}
                onChange={(e, value) => onSaleTypeChange(value)}
                defaultValue={saleTypeObj._values}
                options={saleTypeObj._options || []}
                getOptionLabel={(option) => option.key}
                isOptionEqualToValue={(option, value) => option.value === value.value}
                renderOption={(props, option, { selected }) => (
                    <li {...props}>
                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                        {option.key}
                    </li>
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        fullWidth={false}
                        sx={{ marginTop: '13px', minWidth: '220px' }}
                        label={<Typography variant="h3">{saleTypeObj.name}</Typography>}
                        placeholder={langPack.form.selectOrInput}
                    />
                )}
            />
            <Tooltip
                title={langPack.steps.numberOnly}
                sx={{ display: saleTypeObj._values && saleTypeObj._values.value !== 'normal' ? 'inline-block' : 'none' }}
            >
                <TextField
                    margin="normal"
                    type="number"
                    key={batchObj._values}
                    id="batch"
                    variant="outlined"
                    label={<Typography variant="h3">{batchObj.name}</Typography>}
                    defaultValue={batchObj._values ? batchObj._values.value : ''}
                    onBlur={(e) => onBatchChange(e.target.value)}
                />
            </Tooltip>
        </>
    );
}

App.propTypes = {
    xmlSchema: PropTypes.object,
    langPack: PropTypes.object,
    productModified: PropTypes.object,
    xmlGetField: PropTypes.func,
    onModified: PropTypes.func
};

export default App;
