/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

// material-ui
import { useMediaQuery, Typography, Accordion, AccordionDetails, AccordionSummary } from '@mui/material';
import { ExpandMore as ExpandMoreIcon } from '@mui/icons-material';
import { useTheme } from '@mui/material/styles';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import EditStep from './component/EditStep';
import PhotoGallery from './component/PhotoGallery';
import ProductSummary from './component/ProductSummary';
import ProductAttribute from './component/ProductAttribute';
import ProductService from './component/ProductService';
import ProductRTS from './component/ProductRTS';
import ProductDescription from './component/ProductDescription';
import Confirmation from './component/Confirmation';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //
let productModified = {};
let editMode; // ?   
let productSchema; //
let real_product_id; //id

const App = () => {
    //
    console.log('productEdit app running');
    let localization = useSelector((state) => state.localization); //
    let globalization = useSelector((state) => state.globalization); //
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    let [loginData] = useState(globalization.baseData.loginData);
    let navigate = useNavigate(); //react-router-dom
    //. // & 
    let [activeStep, setActiveStep] = useState(0);
    let [expanded, setExpanded] = useState({ panel1: true, panel2: false, panel3: false, panel4: false });
    let photoCols = useMediaQuery(useTheme().breakpoints.down('md')) ? 3 : 6;
    //
    let urlParams = new URLSearchParams(location.pathname.replace('/product/', ''));
    let product_id = urlParams.get('product_id'); //id
    let group_id = urlParams.get('group_id') || ''; //id
    let category_Id_ext = urlParams.get('category_id'); //id
    editMode = product_id ? 'update' : 'add';
    let [xmlSchema, setXmlSchema] = useState(null);
    let [finalCatId, setFinalCatId] = useState(0); //id
    let [rtsProduct, setRtsProduct] = useState(false); //RTS

    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return; //
    }, [localization.locale]);

    useEffect(() => {
        console.log('I want product editing');
        if (!loginData) return navigate('/');
        if (!editMode) return queryResult(false, langPack.steps.productIdError);
        editMode === 'update' ? getProductInfo({ product_id }) : makeProductInfo({ cat_id: category_Id_ext });
        return () => {
            console.log('I leave product edit');
            productModified = {};
        }; // & 
    }, []);

    //___________________________________________________________
    async function getProductInfo(obj) {
        let paramObj = ma.productDetailQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productDetailCheck(msg);
        let { status, result } = res.response;
        if (!status || !result) return queryResult(status, result);
        //console.log(result);
        let { category_id, rts } = result;
        if (category_id) {
            setFinalCatId(category_id);
        }
        if (product_id) {
            getProductId({ product_id });
        }
        setRtsProduct(rts);
    } //

    function getProductBasicInfo() {
        setRtsProduct(false);
        setFinalCatId(0);
    } //I suppose

    async function getProductId(obj) {
        let paramObj = ma.productIdQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productIdCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        real_product_id = result;
        makeProductExample({ product_id: real_product_id });
        return console.log(result);
    } //

    async function uploadPhotos(photoFile) {
        let currentFolder = -1;
        let paramObj = ma.photoUploadQuery({ file_name: photoFile.name, image_bytes: photoFile, group_id: currentFolder });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.yes);
        let res = ma.photoUploadCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        queryResult(true, langPack.steps.uploadSuccess);
        return result;
    }

    async function makeProductInfo(obj) {
        let paramObj = ma.productSchemaQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productSchemaCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        return makeSchema(result);
    } // 
    //
    async function makeProductExample(obj) {
        let paramObj = ma.productSchemaRenderQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productSchemaRenderCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        return makeSchema(result);
    } //   &  

    function makeSchema(xml) {
        productSchema = new DOMParser().parseFromString(xml, 'text/xml');
        console.log(productSchema);
        setXmlSchema(productSchema);
        window.productSchema = productSchema;
        const errorNode = productSchema.querySelector('parsererror');
        if (errorNode) return queryResult(false, langPack.steps.uneditable);
        return;
    } //

    async function submitProductSchema(xml) {
        let queryMethod;
        let checkMethod;
        let productId;
        let catId;
        if (editMode === 'add') {
            queryMethod = ma.productSchemaAddQuery;
            checkMethod = ma.productSchemaAddCheck;
            catId = category_Id_ext;
        } else {
            queryMethod = ma.productSchemaUpdateQuery;
            checkMethod = ma.productSchemaUpdateCheck;
            productId = real_product_id;
            catId = finalCatId;
        }
        let xmldom = new DOMParser().parseFromString(xml, 'text/xml');
        Array.from(xmldom.querySelectorAll('itemSchema > field > rules, options, label-group, fields')).forEach((node) => node.remove());
        Array.from(xmldom.children[0].children).forEach((node) => (!node.children.length ? node.remove() : ''));
        xml = xmldom.children[0].outerHTML;
        let params = {
            xml,
            cat_id: Number(catId),
            language: localization.locale === 'enUS' ? 'en_US' : 'zh'
        };
        if (productId) params.product_id = productId;
        let paramObj = queryMethod({
            param_product_top_publish_request: JSON.stringify(params)
        });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = checkMethod(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        queryResult(true, editMode === 'add' ? langPack.steps.addSuccess : langPack.steps.updateSuccess);
        return;
    } ///

    function xmlGetField(id) {
        if (!xmlSchema) return;
        let node = xmlSchema.querySelector(`itemSchema > field[id="${id}"]`);
        if (!node) return;
        return insideCheck([node])[0];
    } //

    function insideCheck(nodeArr) {
        let newSubArr = [];
        let subArr = Array.from(nodeArr);
        for (let index = 0; index < subArr.length; index++) {
            let xml = subArr[index];
            let subObj = fieldCheck(xml);
            if (!subObj) subObj = {};
            subObj.xml = xml;
            if (subObj.options && subObj.options.length) subObj.options = insideCheck(subObj.options);
            if (subObj.values && subObj.values.length) subObj.values = insideCheck(subObj.values);
            if (xml.innerHTML && !xml.children.length) subObj.value = xml.innerHTML;
            newSubArr.push(subObj);
        }
        return newSubArr;
    } //

    function generalCheck(node) {
        let attributeObj = node.getAttributeObj();
        if (!attributeObj) attributeObj = {};
        let resultObj = { ...attributeObj, xml: node };
        return resultObj;
    } //

    function fieldCheck(node) {
        let resultObj = generalCheck(node);
        let { type } = resultObj;
        if (!type) return resultObj;
        let optionNodes;
        let valueNodes;
        if (type === 'multiComplex') {
            optionNodes = node.querySelectorAll('fields > field');
            valueNodes = node.querySelectorAll('complex-values > field');
        } else if (type === 'complex') {
            optionNodes = node.querySelectorAll('fields > field');
            valueNodes = node.querySelectorAll('complex-value > field');
        } else if (type === 'multiCheck') {
            optionNodes = node.querySelectorAll('options > option');
            valueNodes = node.querySelectorAll('values > value');
        } else if (type === 'multiInput') {
            valueNodes = node.querySelectorAll('values > value');
        } else if (type === 'singleCheck') {
            optionNodes = node.querySelectorAll('options > option');
            valueNodes = node.querySelectorAll('value');
        } else if (type === 'input') {
            valueNodes = node.querySelectorAll('value');
        }
        if (optionNodes && optionNodes.length) resultObj.options = optionNodes;
        if (valueNodes && valueNodes.length) resultObj.values = valueNodes;
        return resultObj;
    } //

    const onModified = (key, value) => {
        console.log('modified', key, value);
        console.log(value.xml);
        productModified[key] = value;
    }; //

    const modificationSubmission = () => {
        let modifiedKeys = Object.keys(productModified);
        if (!modifiedKeys.length) return queryResult(false, langPack.steps.modificationNull);
        console.log(productModified);
        let xmlArr = Object.values(productModified).map((obj) => obj.xml);
        let finalXmlStr = '<itemSchema>';
        xmlArr.forEach((xml) => (finalXmlStr += xml.outerHTML));
        finalXmlStr += '</itemSchema>';
        let xmldom = new DOMParser().parseFromString(finalXmlStr, 'text/xml').firstChild; //XML
        //Array.from(xmldom.querySelectorAll('field > :not(value, values, complex-value, complex-values)')).forEach((node) => node.remove());
        finalXmlStr = xmldom.outerHTML.replace(/ xmlns="http:\/\/www.w3.org\/1999\/xhtml"/gi, '');
        finalXmlStr = finalXmlStr.replace(/inputvalue/g, 'inputValue');
        finalXmlStr = finalXmlStr.encodeNonASCII(); //ASCII
        submitProductSchema(finalXmlStr);
    };

    const handleAccordionChange = (panel) => (event, isExpanded) => {
        setExpanded((prev) => {
            return { ...prev, [panel]: isExpanded };
        });
    }; //
    //____________Functions return JSX_______________________________________________

    return (
        <MainCard title="Product Edit --">
            <EditStep langPack={langPack} activeStep={activeStep} setActiveStep={setActiveStep} />
            {activeStep === 0 ? (
                <>
                    <PhotoGallery
                        productModified={productModified}
                        xmlSchema={xmlSchema}
                        localization={localization}
                        photoCols={photoCols}
                        rowHeight={200}
                        uploadPhotos={uploadPhotos}
                        xmlGetField={xmlGetField}
                        onModified={onModified}
                        langPack={langPack}
                    />
                    <Accordion expanded={expanded['panel1']} onChange={handleAccordionChange('panel1')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'coral' }}>
                                {langPack.form.KeyInformation}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel1}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductSummary
                                    productModified={productModified}
                                    group_id={group_id}
                                    xmlSchema={xmlSchema}
                                    langPack={langPack}
                                    rtsProduct={rtsProduct}
                                    xmlGetField={xmlGetField}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion expanded={expanded['panel2']} onChange={handleAccordionChange('panel2')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'red' }}>
                                {langPack.form.ProductAttributes}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel2}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductAttribute
                                    productModified={productModified}
                                    xmlSchema={xmlSchema}
                                    langPack={langPack}
                                    xmlGetField={xmlGetField}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion expanded={expanded['panel3']} onChange={handleAccordionChange('panel3')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'orangered' }}>
                                {langPack.form.productService}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel3}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductService
                                    productModified={productModified}
                                    xmlSchema={xmlSchema}
                                    langPack={langPack}
                                    xmlGetField={xmlGetField}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion
                        expanded={expanded['panel4']}
                        onChange={handleAccordionChange('panel4')}
                        sx={{ display: rtsProduct ? 'block' : 'none' }}
                    >
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'orangered' }}>
                                {langPack.form.rtsProduct}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel4}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            {rtsProduct ? (
                                <>
                                    <ProductRTS
                                        productModified={productModified}
                                        xmlSchema={xmlSchema}
                                        langPack={langPack}
                                        xmlGetField={xmlGetField}
                                        onModified={onModified}
                                    />
                                </>
                            ) : (
                                <></>
                            )}
                        </AccordionDetails>
                    </Accordion>
                    <br />
                </>
            ) : activeStep === 1 ? (
                <>
                    <ProductDescription
                        productModified={productModified}
                        localization={localization}
                        xmlGetField={xmlGetField}
                        onModified={onModified}
                        xmlSchema={xmlSchema}
                        langPack={langPack}
                    />
                </>
            ) : activeStep === 2 ? (
                <>
                    <Confirmation langPack={langPack} productModified={productModified} modificationSubmission={modificationSubmission} />
                </>
            ) : (
                <></>
            )}
        </MainCard>
    );
};

export default App;
