import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Typography, Autocomplete, Checkbox, Chip, Button, MenuItem, TextField, Divider, Tooltip } from '@mui/material';
import { Add as AddIcon, Check as CheckIcon, Cancel as CancelIcon, DeleteOutline as DeleteIcon } from '@mui/icons-material';

let attributeSaver;
function App({ langPack, xmlSchema, productModified, xmlGetField, onModified }) {
    console.log('attributes running');
    //
    let [attributeObj, setAttributeObj] = useState({}); //
    let [addAttribute, setAddAttribute] = useState(false); //
    let [optionSelected, setOptionSelected] = useState(''); //

    useEffect(() => {
        if (!xmlSchema) return;
        let { icbuCatProp } = productModified;
        icbuCatProp ? setAttributeObj(icbuCatProp) : getAttributeObj();
        return;
    }, [xmlSchema]);

    let checkValues = (arr) => {
        if (!arr || !arr.length) return arr;
        let newArr = [];
        arr.forEach((obj) => {
            let newObj = { ...obj };
            let { type, displayName, inputValue, value, values } = newObj;
            if (value !== undefined) {
                let valueObj = {};
                if (displayName !== undefined) valueObj.key = displayName;
                if (inputValue !== undefined) valueObj.key = inputValue;
                if (value !== undefined) valueObj.value = value;
                if (Object.keys(valueObj).length) newObj = { ...newObj, ...valueObj };
            } else if (['input', 'singleCheck'].find((str) => str === type)) {
                let valueObj = {};
                if (values[0].inputValue !== undefined) valueObj.key = values[0].inputValue;
                if (values[0].value !== undefined) valueObj.value = values[0].value;
                if (Object.keys(valueObj).length) newObj._values = valueObj;
            } else {
                let newValues = checkValues(values);
                if (newValues) newObj._values = newValues;
            }
            newArr.push(newObj);
        });
        return newArr;
    };
    let checkOptions = (arr) => {
        if (!arr || !arr.length) return arr;
        let newArr = [];
        arr.forEach((obj) => {
            let newObj = { ...obj };
            let { type, options, displayName, inputValue, value } = newObj;
            if (value !== undefined) {
                let optionObj = {};
                if (displayName !== undefined) optionObj.key = displayName;
                if (inputValue !== undefined) optionObj.key = inputValue;
                if (value !== undefined) optionObj.value = value;
                if (Object.keys(optionObj).length) newObj = { ...newObj, ...optionObj };
            } else if (/input/gi.test(type)) {
                newObj._options = false;
            } else {
                let newOptions = checkOptions(options);
                if (newOptions) {
                    newObj._options = newOptions;
                } else {
                    let optionObj = {};
                    if (options && options[0].displayName !== undefined) optionObj.key = options[0].displayName;
                    //if (options && options[0].inputValue !== undefined) optionObj.key = options[0].inputValue;
                    if (options && options[0].value !== undefined) optionObj.value = options[0].value;
                    if (Object.keys(optionObj).length) newObj._options = [optionObj];
                }
            }
            newArr.push(newObj);
        });
        return newArr;
    };

    function getAttributeObj() {
        let fieldObj = xmlGetField('icbuCatProp');
        if (!fieldObj || fieldObj._values || fieldObj._options) return;
        if (fieldObj.values) {
            fieldObj._values = fieldObj.values.filter((obj) => obj.name).sort((a, b) => a.type.length - b.type.length);
            fieldObj._values = checkValues(fieldObj._values);
        }
        if (fieldObj.options) {
            fieldObj._options = fieldObj.options.filter((obj) => obj.name).sort((a, b) => a.type.length - b.type.length);
            fieldObj._options = checkOptions(fieldObj._options);
        }
        //console.log(fieldObj);
        setAttributeObj(fieldObj);
    } //

    function saveAttribute(newObj, cleaner) {
        let valueArr = newObj._values;
        let invalidList = []; //
        let setter = setAttributeObj;
        let valueTagName = `complex-value`;
        let newField = new DOMParser().parseFromString(newObj.xml.outerHTML, 'text/xml').firstChild; //
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueArr.forEach((obj, index) => {
            let { type, id, _values } = obj;
            let valueTemplates = newField.querySelector(`fields > field#${id}`); //
            if (valueTemplates) {
                valueTemplates.innerHTML = ''; //
                if (/multi(?!Complex)/.test(type)) {
                    let valueStr = '';
                    _values.forEach((subObj) => {
                        let { key, value } = subObj;
                        valueStr += `<value inputValue="${key}">${value}</value>`;
                    });
                    let elementValues = document.createElement('values');
                    elementValues.innerHTML = valueStr;
                    if (valueStr) {
                        valueTemplates.innerHTML = elementValues.outerHTML;
                        valueSaver += valueTemplates.outerHTML;
                    } else {
                        invalidList.push(index);
                    }
                } else {
                    let { key, value } = _values;
                    let elementValue = document.createElement('value');
                    elementValue.setAttribute('inputValue', key);
                    elementValue.innerText = value;
                    const checkContent = (str) => str !== undefined && String(str).trim() !== '' && str !== null; //
                    if (checkContent(key)) {
                        valueTemplates.innerHTML = elementValue.outerHTML;
                        valueSaver += valueTemplates.outerHTML;
                    } else {
                        invalidList.push(index);
                    }
                }
            }
        });
        valueSaver += `</${valueTagName}>`;
        if (invalidList.length && cleaner) newObj._values = valueArr.filter((obj, index) => !invalidList.find((i) => i === index));
        if (newObj.xml.querySelector(valueTagName)) newObj.xml.querySelector(valueTagName).remove(); //
        let newXml = new DOMParser().parseFromString(valueSaver, 'text/xml').firstChild; //
        newObj.xml.append(newXml); //
        setter(newObj); //JSX
        return onModified('icbuCatProp', newObj); //
    }

    const onAttributeConfirm = () => {
        console.log('confirming');
        if (!attributeSaver) return console.log('');
        let [mainAttributeObj, value] = attributeSaver;
        // need to find the main attribute option first (by id),
        //and then map the data with options(if any),
        //  and then update to the main attribute
        let { id, type, _values } = mainAttributeObj; //multicheck, singlecheck, input
        let newObj = { ...attributeObj }; //make a copy
        let valueArr = newObj._values;
        let myOptions = attributeObj._options.find((option) => option.id === id); //
        if (/multi(?!Complex)/.test(type)) {
            _values = [];
            value.forEach((str) => {
                if (myOptions._options) {
                    let subOptionObj = myOptions._options.find((subOption) => subOption.key === str); //options are mapped with names
                    if (subOptionObj) _values.push(subOptionObj);
                    //find the mapping, and save the data
                    else _values.push({ key: '', value: str }); //worst situation ( no matches found)
                } else {
                    _values.push({ key: '', value: str }); //worst situation ( no matches found)
                }
            }); //
        } else {
            if (myOptions._options) {
                let subOptionObj = myOptions._options.find((subOption) => subOption.key === value); //options are mapped with names
                if (subOptionObj) _values = subOptionObj;
                //find the mapping, and save the data
                else _values = { key: value, value: '' }; //worst situation ( no matches found)
            } else {
                _values.key = value; //worst situation ( no matches found)
            } //
        }
        mainAttributeObj._values = _values;
        valueArr = valueArr.filter((obj) => obj.id !== id); //
        valueArr.push(mainAttributeObj);
        newObj._values = valueArr;
        saveAttribute(newObj);
        return;
    };

    const onAttributeChange = (mainAttributeObj, value) => {
        attributeSaver = [mainAttributeObj, value];
        return;
    };

    const onOptionChange = (values) => {
        setOptionSelected(values);
        return;
    };

    const onOptionConfirm = () => {
        if (!optionSelected) return;
        let optionArr = optionSelected.map((value) => attributeObj._options.find((obj) => obj.name === value));
        let newObj = { ...attributeObj };
        let newValueArr = newObj._values || [];
        optionArr.forEach((optionObj) => {
            if (!newValueArr.length || !newValueArr.find((valueObj) => valueObj.id === optionObj.id)) {
                let { id, name, type } = optionObj;
                let newValue = { id, name, type };
                newValue._values = /multi(?!Complex)/.test(type) ? [] : {};
                newValueArr.push(newValue);
            } //
        });
        newObj._values = newValueArr;
        setAttributeObj(newObj);
        setOptionSelected('');
        setAddAttribute((prev) => !prev);
        return;
    };

    const onAttributeClear = () => {
        console.log('clearing');
        let newObj = { ...attributeObj };
        return saveAttribute(newObj, true);
    }; //

    return (
        <>
            <div style={{ paddingTop: '10px', paddingBottom: '20px' }}>
                {addAttribute ? (
                    <>
                        {attributeObj._options && attributeObj._options.length ? (
                            <span style={{ display: 'flex', flexDirection: 'row', alignItems: 'stretch' }}>
                                <Autocomplete
                                    id="attribute-options"
                                    key={attributeObj._options ? attributeObj._options : 'attribute-options'}
                                    sx={{ display: 'inline' }}
                                    multiple
                                    disableCloseOnSelect={true}
                                    onChange={(e, value) => onOptionChange(value)}
                                    options={attributeObj._options.map((obj) => obj.name)}
                                    renderOption={(props, option, { selected }) => (
                                        <li {...props}>
                                            <Checkbox style={{ marginRight: 8 }} checked={selected} />
                                            {option}
                                        </li>
                                    )}
                                    renderInput={(params) => (
                                        <TextField
                                            {...params}
                                            fullWidth={false}
                                            sx={{ marginTop: '13px', minWidth: '220px' }}
                                            label={<Typography variant="h3">{attributeObj.name}</Typography>}
                                            placeholder={langPack.form.selectOrInput}
                                        />
                                    )}
                                />
                                <Button startIcon={<CheckIcon />} onClick={onOptionConfirm}>
                                    {langPack.common.yes}
                                </Button>
                                <Button startIcon={<CancelIcon />} onClick={() => setAddAttribute((prev) => !prev)}>
                                    {langPack.common.no}
                                </Button>
                            </span>
                        ) : (
                            <Typography variant="body1">{langPack.form.attributeMissing}</Typography>
                        )}
                    </>
                ) : (
                    <>
                        <Tooltip title={langPack.steps.addAttributes}>
                            <Button startIcon={<AddIcon />} onClick={() => setAddAttribute((prev) => !prev)}>
                                {langPack.steps.addAttributes}
                            </Button>
                        </Tooltip>
                        <Tooltip title={langPack.steps.clearAttributes}>
                            <Button startIcon={<DeleteIcon />} onClick={onAttributeClear}>
                                {langPack.steps.clearAttributes}
                            </Button>
                        </Tooltip>
                    </>
                )}
            </div>
            <Divider />
            <br />
            {attributeObj._values && attributeObj._values.length ? (
                attributeObj._values.map((obj) => {
                    return !/input/gi.test(obj.type) ? (
                        /multi(?!Complex)/.test(obj.type) ? ( //multiComplexAPI
                            <Autocomplete
                                id="current-attributes-multiple"
                                key={obj.id + JSON.stringify(obj._values)}
                                sx={{ display: 'inline' }}
                                limitTags={1}
                                multiple
                                disableCloseOnSelect={true}
                                onBlur={onAttributeConfirm}
                                onChange={(e, value) => onAttributeChange(obj, value)}
                                options={attributeObj._options.find((option) => option.id === obj.id)._options.map((obj) => obj.key)}
                                defaultValue={obj._values.map((obj) => obj.key)}
                                renderTags={(options, getTagProps) =>
                                    options.map((option, index) => <Chip variant="outlined" label={option} {...getTagProps({ index })} />)
                                }
                                renderOption={(props, option, { selected }) => (
                                    <li {...props}>
                                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                                        {option}
                                    </li>
                                )}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        fullWidth={false}
                                        sx={{ marginTop: '13px', minWidth: '220px' }}
                                        label={<Typography variant="h3">{obj.name + ' _ ' + obj.type}</Typography>}
                                        placeholder={langPack.form.selectOrInput}
                                    />
                                )}
                            />
                        ) : (
                            <Autocomplete
                                id="current-attributes-single"
                                key={obj.id + JSON.stringify(obj._values)}
                                sx={{ display: 'inline' }}
                                onBlur={onAttributeConfirm}
                                onChange={(e, value) => onAttributeChange(obj, value)}
                                defaultValue={obj._values.key}
                                options={attributeObj._options.find((option) => option.id === obj.id)._options.map((obj) => obj.key)}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        fullWidth={false}
                                        sx={{ marginTop: '13px', minWidth: '220px' }}
                                        label={<Typography variant="h3">{obj.name + ' _ ' + obj.type}</Typography>}
                                        placeholder={langPack.form.selectOrInput}
                                    />
                                )}
                            />
                        )
                    ) : (
                        <TextField
                            key={obj.id + JSON.stringify(obj._values)}
                            id="current-attributes-input-only"
                            fullWidth={false}
                            defaultValue={obj._values.key || ''}
                            onBlur={onAttributeConfirm}
                            onChange={(e) => onAttributeChange(obj, e.target.value)}
                            sx={{ marginTop: '13px', minWidth: '220px' }}
                            label={<Typography variant="h3">{obj.name + ' _ ' + obj.type}</Typography>}
                            placeholder={langPack.form.selectOrInput}
                        />
                    );
                })
            ) : (
                <></>
            )}
        </>
    );
}

App.propTypes = {
    langPack: PropTypes.object,
    xmlSchema: PropTypes.object,
    productModified: PropTypes.object,
    xmlGetField: PropTypes.func,
    onModified: PropTypes.func
};

export default App;
